@import '../../base';
@import '../../baseColors';

.hse-LinkAbbr {
  position: relative;
  text-decoration: none;
  color: $color-text-brand;

  &::after {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 1px;
    content: '';
    border-bottom: 1px dashed currentColor;
  }

  &:hover {
    color: $color-text-brand;
    &::after {
      border-bottom: none;
    }
  }

  &:focus-visible {
    outline: none;
    &::after {
      border-bottom: none;
    }
  }
}

.hse-LinkAbbr__tooltip {
  display: inline;
  vertical-align: inherit;
}

.hse-TooltipTarget.hse-LinkAbbr__tooltipTarget {
  display: inline;
  vertical-align: inherit;
}
