@import '../../base';
@import '../../baseText';
$user-menu-width: 340px;

.hse-UserMenu {
  max-width: $user-menu-width;
  width: 100%;
  border-radius: $radius-small;
  background: $color-white;
  box-shadow: #{$shadow-dropdown};
}

.hse-UserMenu__person {
  flex-shrink: 0;
  width: $sizing-64;
  height: $sizing-64;
  margin-right: $spacing-small-x;
  border-radius: $radius-medium;
}
.hse-UserMenu__header {
  display: flex;
  align-items: center;
  padding: $spacing-small;
  border-bottom: 1px solid $color-gray-morn-3;
}
.hse-UserMenu__footer {
  padding: $spacing-small-2x + $spacing-small-3x $spacing-small $spacing-small;
}
.hse-UserMenu__info {
}
.hse-UserMenu__name {
  @include text_caption_medium_bold;
  margin-bottom: $spacing-small-3x / 2;
  color: $color-black;
}
.hse-UserMenu__role {
  @include text_caption_x_small_bold;
  color: $color-text-gray-morn-1;
}
.hse-UserMenu__menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
.hse-UserMenu__item {
  padding: $spacing-small-3x $spacing-small-2x;
  &:last-child {
    margin-bottom: 0;
  }
  &::before {
    content: none;
  }
}
.hse-UserMenu__link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;

  @include focus-visible {
    outline: none;
    @include focus-shadow;
  }
}
.hse-UserMenu__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: $spacing-small-3x;
  color: $color-gray-dusk-1;
  flex: 0 0 $icon-size-small;
}
.hse-UserMenu__text {
  @include text_caption_small_bold;
  color: $color-gray-dusk-1;
}
