@import '../../../base';
@import '../mixins';
@import '../vars';

.hse-Calendar_WeekLabels {
  @include Calendar_grid_template_7;
  margin-top: 8px;
  gap: 0 8px;
}

.hse-Calendar_WeekLabels_day {
  @include Calendar_button;
  width: 32px;
  height: 32px;
  cursor: inherit;
  border-radius: $radius-medium;
}

.hse-Calendar_DayGrid {
  @include Calendar_grid_template_7;
  margin-top: 8px;
  gap: 8px 8px;
}

.hse-Calendar_button_light {
  font-weight: 400;
}
.hse-Calendar_button_selectable.hse-Calendar_button_light {
  color: $color-text-base-black;
}
.hse-Calendar_button_dif_month.hse-Calendar_button_light {
  color: $color-gray-dusk-4;
}
