@import '../base';

$date-picker-width: 136px;

:root {
  --hse-date-picker-width: #{$date-picker-width};
}

.hse-DatePicker {
  position: relative;

  .hse-Popover__target {
    width: 100%;
  }
}

.hse-DatePicker__input {
  width: var(--hse-date-picker-width, $date-picker-width);
  color: $color-text-gray-dusk-1;
}
