@import '../../base';

$time-picker-width: 136px;

:root {
  --hse-time-picker-width: #{$time-picker-width};
}

.hse-TimePicker {
  width: var(--hse-time-picker-width, #{$time-picker-width});

  .hse-Popover__target {
    width: 100%;
  }
}

.hse-TimePicker__input {
  color: $color-text-gray-dusk-1;
}
