@import '../../base';

@mixin Calendar_transition {
  transition: all 0.2s ease-out;
}

@mixin Calendar_button {
  @include text_caption_small_bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: $color-text-gray-dusk-1;
}

@mixin Calendar_grid_template_7 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: '. . . . . . .';
  place-items: center;
}

@mixin Calendar_grid_template_3 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: '. . .';
  gap: $gap-default;
  place-items: center;
}
