@import '~@hse-design/core/src/base';

.hse-fade-enter {
  opacity: 0;
}

.hse-fade-enter-active {
  transition: opacity $transition-quick;
  opacity: 1;
}

.hse-fade-exit {
  opacity: 1;
}

.hse-fade-exit-active {
  transition: opacity $transition-quick;
  opacity: 0;
}

.hse-slide-from-left-enter,
.hse-slide-from-left-exit-done {
  opacity: 0;
  transform: translateX(-100%);
}

.hse-slide-from-left-enter-active {
  transition: transform $transition-quick, opacity $transition-quick;
  opacity: 1;
  transform: translateX(0);
}

.hse-slide-from-left-exit {
  opacity: 1;
  transform: translateX(0);
}

.hse-slide-from-left-exit-active {
  transition: transform $transition-quick, opacity $transition-quick;
  opacity: 0;
  transform: translateX(-100%);
}

.hse-slide-from-right-enter,
.hse-slide-from-right-exit-done {
  opacity: 0;
  transform: translateX(100%);
}

.hse-slide-from-right-enter-active {
  transition: transform $transition-quick, opacity $transition-quick;
  opacity: 1;
  transform: translateX(0);
}

.hse-slide-from-right-exit {
  opacity: 1;
  transform: translateX(0);
}

.hse-slide-from-right-exit-active {
  transition: transform $transition-quick, opacity $transition-quick;
  opacity: 0;
  transform: translateX(100%);
}
