@import '../../../base';
@import '../mixins';
@import '../vars';

.hse-Calendar_yearsGrid {
  @include Calendar_grid_template_3;

  max-height: 170px;
  margin-top: 24px;
  margin-bottom: 16px;

  overflow-y: auto;
  direction: ltr;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: $radius-small;
    background: $color-gray-dusk;
  }
}
