@import '../../base';
$side-pane-width: 344px;

.hse-SidePaneWrapper {
  position: fixed;
  z-index: $z-index-max;
  top: 0;
  bottom: 0;
  width: $side-pane-width;
}

.hse-SidePaneWrapper_position_left {
  left: 0;

  &.hse-SidePaneWrapper_collapsed .hse-SidePane__closeOuter {
    left: $spacing-small-x;
  }

  .hse-SidePane__closeOuter {
    left: $side-pane-width + $spacing-small-x;
  }
}

// todo use tokens
.hse-SidePaneWrapper_position_right {
  right: 0;

  &.hse-SidePaneWrapper_collapsed .hse-SidePane__closeOuter {
    right: $spacing-small-x;
  }

  .hse-SidePane__closeOuter {
    right: $side-pane-width + $spacing-small-x;
  }

  .hse-SidePane__iconOuter {
    transform: rotate(-180deg);
  }
  .hse-SidePane__iconOuter_rotate {
    transform: rotate(0);
  }
}

// todo use tokens
.hse-SidePane__close {
  position: absolute;
  top: $spacing-small-x;
  right: $spacing-small-x;
  width: $sizing-24;
  height: $sizing-24;
  padding: 5px; // todo remove
  cursor: pointer;
  border: none;
  outline: none;
  background-color: $color-white;
}

// todo use normal icon
.hse-SidePane__icon {
  position: relative;
  display: block;
  width: 14px;
  height: 14px;
  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 2px;
    height: 14px;
    content: '';
    transform: translate(-50%, -50%) rotate(-45deg);
    background-color: $color-gray-dusk-3;
  }
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 14px;
    height: 2px;
    content: '';
    transform: translate(-50%, -50%) rotate(-45deg);
    background-color: $color-gray-dusk-3;
  }
}

// todo use tokens
.hse-SidePane__closeOuter {
  position: fixed;
  top: $spacing-small-x;
  display: flex;
  align-items: center;
  justify-content: center;
  width: $sizing-32;
  height: $sizing-32;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
  background-color: $color-white;

  @include focus-visible {
    @include focus-shadow;
  }
}

// todo use tokens
.hse-SidePane__iconOuter {
  position: relative;
  display: block;
  width: $sizing-24;
  height: $sizing-24;
  transition: transform $transition-quick ease-in-out;
}

.hse-SidePane__iconOuter_rotate {
  transform: rotate(-180deg);
}

.hse-SidePaneAnimationWrapper {
  display: flex;
  width: $side-pane-width;
  height: 100vh;
  background: $color-background-white;
  box-shadow: #{$shadow-hover};
}

.hse-SidePane {
  position: relative;
  width: 100%;
  height: 100%;
  padding: $spacing-small;
}
