@import '../../base';
@import './mixins';
@import './vars';

.hse-Calendar_button {
  @include Calendar_transition;
  @include Calendar_button;
  border-radius: $radius-medium;
  border: none;

  @include focus-visible {
    @include focus-shadow;
  }

  &:focus {
    outline: none;
  }
}

.hse-Calendar_button_selectable {
  z-index: 3;
  width: 100%;
  border-radius: 4px;
  &:hover {
    color: $color-brand-1;
    background-color: $color-brand-5;
  }
}

.hse-Calendar_button_selected {
  z-index: 3;
  background: $color-brand-1 !important;
  color: $color-brand-5 !important;
  width: 100%;
}

.hse-Calendar_button_target {
  z-index: 3;
  color: $color-brand-1;
  background-color: $color-brand-5;
}

.hse-Calendar_button_gap_selectable_filler {
  z-index: 0;
  @include Calendar_transition;
  transform-style: preserve-3d;

  &:after {
    content: '';
    position: absolute;
    display: inline-block;
    transform: translateZ(-1px);
    height: 32px;
    width: 40px;
    background-color: $color-brand-5;
    pointer-events: none;
    left: 16px;
  }
}

.hse-Calendar_button_current {
  color: $color-brand-1;
}

.hse-Calendar_button_dif_month {
  color: $color-gray-dusk-4;
}

.hse-Calendar_button_dif_month_selected {
  border: 1px solid $color-brand-1;
  color: $color-brand-1;
}

.hse-Calendar_button_size_small {
  width: $button-day-default-size;
  height: $button-day-default-size;
}

.hse-Calendar_button_size_large {
  padding: $padding-default;
}
