@import '../../base';

$track-width: $spacing-small-2x;
$thumb-width: $spacing-small-3x;

.hse-ScrollArea {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.hse-ScrollArea_modify {
  .simplebar-track.simplebar-horizontal {
    height: $track-width;
    margin-right: $track-width;
    border-radius: $radius-large;
    .simplebar-scrollbar {
      height: $thumb-width;
      border-radius: $radius-large;
      &.simplebar-visible {
        background: $color-gray-morn-1;
        &.simplebar-hover {
          background: $color-gray-dusk-3;
        }
      }
      &::before {
        content: none;
      }
    }
  }
  .simplebar-track.simplebar-vertical {
    width: $track-width;
    margin-bottom: $track-width;
    border-radius: $radius-large;
    .simplebar-scrollbar {
      width: $thumb-width;
      border-radius: $radius-large;
      &.simplebar-visible {
        background: $color-gray-morn-1;
        &.simplebar-hover {
          background: $color-gray-dusk-3;
        }
      }
      &::before {
        content: none;
      }
    }
  }
  .simplebar-dragging {
    .simplebar-track.simplebar-horizontal {
      .simplebar-scrollbar.simplebar-visible {
        background: $color-gray-dusk-2;
      }
      .simplebar-scrollbar.simplebar-visible.simplebar-hover {
        background: $color-gray-dusk-2;
      }
    }
    .simplebar-track.simplebar-vertical {
      .simplebar-scrollbar.simplebar-visible {
        background: $color-gray-dusk-2;
      }
      .simplebar-scrollbar.simplebar-visible.simplebar-hover {
        background: $color-gray-dusk-2;
      }
    }
  }
}
