.hse-SegmentedControl {
  position: relative;
  display: inline-flex;
  transition: border-color ease 0.2s;
  vertical-align: middle;
  border: 2px solid transparent;
  border-radius: 6px;
  outline: none; }
  .hse-SegmentedControl,
  .hse-SegmentedControl > * {
    box-sizing: content-box;
    user-select: none;
    background: transparent; }

.hse-SegmentedControl:focus-visible,
.hse-SegmentedControl_focus {
  border-color: #c2daff; }

.hse-SegmentedControl_size_small .hse-SegmentedControl__item {
  padding: 5px 13px; }

.hse-SegmentedControl_size_medium .hse-SegmentedControl__item {
  padding: 9px 13px; }

.hse-SegmentedControl_size_large .hse-SegmentedControl__item {
  padding: 13px; }

.hse-SegmentedControl__item {
  font-family: "Proxima Nova", system-ui, -apple-system, "Segoe UI", "Roboto", "Ubuntu", "Cantarell", "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  padding: unset;
  border: none;
  position: relative;
  display: block;
  cursor: pointer;
  transition: all ease 0.2s;
  text-align: center;
  color: #0050cf;
  border: 1px solid #d3d8e6; }
  .hse-SegmentedControl__item:focus {
    outline: none;
    outline-offset: initial; }
  .hse-SegmentedControl__item::after {
    position: absolute;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    content: '';
    transition: opacity ease 0.2s;
    opacity: 0;
    border: 1px solid #0050cf; }

.hse-SegmentedControl__item:first-child,
.hse-SegmentedControl__item:first-child::after {
  border-radius: 4px 0 0 4px; }

.hse-SegmentedControl__item:last-child,
.hse-SegmentedControl__item:last-child::after {
  border-radius: 0 4px 4px 0; }

.hse-SegmentedControl__item:not(:first-child) {
  margin-left: -1px; }

.hse-SegmentedControl__item:focus,
.hse-SegmentedControl__item_focus,
.hse-SegmentedControl__item:focus-visible,
.hse-SegmentedControl__item:hover,
.hse-SegmentedControl__item_hover {
  background: #f0f5ff; }

.hse-SegmentedControl__item:active,
.hse-SegmentedControl__item_active {
  z-index: 1;
  background: #f0f5ff; }
  .hse-SegmentedControl__item:active::after,
  .hse-SegmentedControl__item_active::after {
    opacity: 1; }

.hse-SegmentedControl__item_inactive,
.hse-SegmentedControl__item_disabled,
.hse-SegmentedControl__item:disabled {
  user-select: none;
  pointer-events: none;
  color: #6b7a99; }
  .hse-SegmentedControl__item_inactive::after,
  .hse-SegmentedControl__item_disabled::after,
  .hse-SegmentedControl__item:disabled::after {
    content: none; }

