@import '../../base';

$image-cropper-width: 764px;
$image-cropper-progress-height: 86px;

.hse-ImageCropper {
  width: $image-cropper-width;
}

.hse-ImageCropper_modify {
  .point-e,
  .point-n,
  .point-w,
  .point-s {
    display: none;
  }
  .cropper-container {
    z-index: 1;
  }
  .cropper-bg {
    background: $color-background-gray-2;
    background-image: none;
  }
  .cropper-view-box {
    outline: 2px solid $color-white;
  }
  .cropper-point.point-ne {
    width: 9px;
    height: 9px;
    border-top: 4px solid $color-white;
    border-right: 4px solid $color-white;
    border-bottom: none;
    border-left: none;
    background-color: transparent;
  }
  .cropper-point.point-nw {
    width: 9px;
    height: 9px;
    border-top: 4px solid $color-white;
    border-right: none;
    border-bottom: none;
    border-left: 4px solid $color-white;
    background-color: transparent;
  }
  .cropper-point.point-sw {
    width: 9px;
    height: 9px;
    border-top: none;
    border-right: none;
    border-bottom: 4px solid $color-white;
    border-left: 4px solid $color-white;
    background-color: transparent;
  }
  .cropper-point.point-se {
    width: 9px;
    height: 9px;
    border-top: none;
    border-right: 4px solid $color-white;
    border-bottom: 4px solid $color-white;
    border-left: none;
    background-color: transparent;
  }
  .cropper-center {
    display: none;
  }
}

.hse-ImageCropperContainer {
  position: relative;
  width: 100%;
  min-height: $image-cropper-progress-height + $spacing-medium;
  margin-bottom: $spacing-medium;
}

.hse-ImageCropperContainer__progress {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $color-gray-morn-3;
}
.hse-ImageCropperProgress {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 480px;
  height: $image-cropper-progress-height;
  border-radius: $radius-medium;
  background-color: $color-white;
}
.hse-ImageCropperProgress__modify {
  .hse-LabeledProgressBar__label {
    margin-bottom: 8px;
    color: $color-text-base-black;
    font-size: 14px;
  }
  .hse-LabeledProgressBar__description {
    margin-top: 8px;
    color: $color-gray-dusk-1;
    font-size: 14px;
  }
}
.hse-ImageCropper__icon-container {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 24px;
  height: 24px;
  padding: 5px;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: $color-white;
}
.hse-ImageCropper__icon {
  position: relative;
  display: block;
  width: 14px;
  height: 14px;
  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 2px;
    height: 14px;
    content: '';
    transform: translate(-50%, -50%) rotate(-45deg);
    background-color: $color-gray-morn-1;
  }
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 14px;
    height: 2px;
    content: '';
    transform: translate(-50%, -50%) rotate(-45deg);
    background-color: $color-gray-morn-1;
  }
}

.hse-ImageCropperControlPanel {
  display: flex;
  align-items: center;
}

.hse-ImageCropperControlPanel__left {
  display: flex;
  align-items: center;
}

.hse-ImageCropperControlPanel__right {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: auto;
}

.hse-ImageCropperControlPanel__button {
  @include text_caption_small_bold;
  margin-left: auto;
  padding: 14px 25px;
  cursor: pointer;
  color: $color-white;
  border: none;
  border-radius: 4px;
  outline: none;
  background-color: #0050cf;
}
.hse-ImageCropperRotate {
  margin-right: $spacing-small-2x;
}
.hse-ImageCropperRotate__button {
  width: 48px;
  height: 48px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  background-color: #edeff3;
}
.hse-ImageCropperRotate__icon {
  color: $color-gray-dusk-1;
}
.hse-ImageCropperAspectRatio {
  display: flex;
  overflow: hidden;
  align-items: center;
  border-radius: 4px;
}
.hse-ImageCropperAspectRatio__button {
  @include text_caption_small_bold;
  position: relative;
  width: 68px;
  height: 48px;
  cursor: pointer;
  color: $color-gray-dusk-1;
  border: none;
  outline: none;
  background-color: #edeff3;
  &:not(:last-child)::after {
    position: absolute;
    top: 0;
    right: 0;
    width: 1px;
    height: 100%;
    content: '';
    background-color: $color-gray-morn-2;
  }
  &:hover {
    color: $color-brand-1;
    background-color: rgba($color-brand-1, 0.1);
  }
}
.hse-ImageCropperAspectRatio__button_selected {
  color: $color-brand-1;
  background-color: rgba($color-brand-1, 0.1);
}
.hse-ImageCropperFileInput {
  position: relative;
}
.hse-ImageCropperFileInput__button {
  display: flex;
  align-items: center;
  height: 48px;
  cursor: pointer;
  border: none;
  outline: none;
}
.hse-ImageCropperFileInput__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.hse-ImageCropperFileInput__input {
  display: none;
}
.hse-ImageCropperFileInput__icon {
  margin-right: 7px;
  color: $color-gray-dusk-1;
}
.hse-ImageCropperFileInput__text {
  @include text_caption_small_bold;
  color: $color-gray-dusk-1;
}
