@import '../../base';
@import './SidebarItemMixins';

.hse-SidebarItemCollapseItem {
  cursor: pointer;
  overflow: hidden;
  display: flex;
  align-items: center;
  height: 0;
  width: 100%;
  transform: translateY(-20%);
  opacity: 0;
  position: relative;
  z-index: 2;
  transition: all 0.3s;
  @include text_caption_medium_regular;
  color: $color-gray-dusk-1;
  text-decoration: none;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 24px;
  @include respond-down('tablet-large') {
    @include text_caption_small_regular;
  }
}

.hse-SidebarItemCollapse_isOpen .hse-SidebarItemCollapseItem {
  overflow: visible;
  height: 56px;
  transform: translateY(0);
  opacity: 1;
}

.hse-SidebarItemCollapseItem {
  @include text_caption_medium_regular;
  color: $color-gray-dusk-1;
  text-decoration: none;
  width: 100%;
  @include respond-down('tablet-large') {
    @include text_caption_small_regular;
  }
}

.hse-SidebarItemCollapseItem::before {
  height: 40px;
  position: absolute;
  z-index: 2;
  left: 0;
  width: 2px;
  content: '';
  transition: background-color $transition-quick ease-out;
  background: var(--background-color);
}

.hse-SidebarItemCollapseItem_selected {
  color: $color-brand;
}

.hse-SidebarItemCollapseItem_selected::before {
  background: $color-brand;
}

.hse-SidebarItemCollapseItem:hover {
  color: $color-brand;
  background-color: rgba($color-brand, $opacity-10);
}

.hse-SidebarItemCollapseItem:active {
  color: $color-brand;
  background-color: rgba($color-brand, $opacity-10);
}
.hse-SidebarItemCollapseItem:focus-visible {
  outline: none;
  color: $color-brand;
  box-shadow: 0 0 0 2px $color-brand-4;
}

.hse-SidebarItemCollapseItem_contentLeft {
  padding: 0 $spacing-medium-x 0 $spacing-large-3x;
  .hse-Button__content {
    justify-content: flex-start;
  }
  .hse-SidebarItemCollapseItem__span {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
