@import '../../base';
@import '~@hse-design/tokens/lib/scss/breakpoints';

.hse-MobileMenu {
  position: fixed;
  z-index: $z-index-mobile-menu;
  top: $header-height-small;
  bottom: 0;
  left: 0;
  width: 100%;
  max-width: $breakpoint-mobile-large;
  padding: $spacing-small 0 0;
}

// todo: придумать решение лучше
.hse-MobileMenu_overrideSidebar {
  .hse-Sidebar_variant_static {
    position: initial;
    width: 100%;
    height: 100%;
    padding-top: 0;
  }
  .hse-Sidebar__wrapper {
    overflow-y: auto;
  }
}
