// Позволяет корректно работать полифиллу focus-visible
// При сборке ДС применяется плагин postcss-focus-visible,
// поэтому этот миксин внутри ДС не используется
// Используйте его в продуктовых проектах, если нет возможности
// подключить postcss-плагин
@mixin focus-visible {
  // Временно отключено из-за проблем в Safari
  //&:focus-visible {
  //  @content;
  //}
  &.focus-visible {
    @content;
  }
}
