@import '../base';
@import '../baseText';

.hse-Tag {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  vertical-align: middle;
  color: $color-brand;
  border: none;
  border-radius: $radius-small;
  background-color: rgba($color-brand, $opacity-10);
  &:focus {
    border: none;
    outline: none;
  }
}

.hse-Tag:focus,
.hse-Tag_focused {
  @include focus-shadow;
  color: $color-base-white;
  background-color: $color-brand-dark;
}

.hse-Tag_variant_primary {
  color: $color-brand;
  background-color: rgba($color-brand, $opacity-10);
}

.hse-Tag_variant_primary.hse-Tag_selected {
  color: $color-base-white;
  background-color: $color-brand-1;
}
.hse-Tag_variant_primary.hse-Tag_focused,
.hse-Tag_variant_primary:focus {
  @include focus-shadow;
  color: $color-base-white;
  background-color: $color-brand-dark;
}
.hse-Tag_variant_primary.hse-Tag_hovered,
.hse-Tag_variant_primary:hover {
  color: $color-base-white;
  background-color: $color-brand-dark;
}
.hse-Tag_variant_primary.hse-Tag_disabled,
.hse-Tag_variant_primary:disabled {
  color: $color-gray-dusk-3;
  border: 1px solid rgba($color-gray-dusk-4, $opacity-10);
  background-color: $color-base-white;
}

.hse-Tag_variant_primaryInvert {
  color: $color-base-white;
  background-color: $color-element-overlay-light-op40;
}

.hse-Tag_variant_primaryInvert.hse-Tag_selected {
  color: $color-brand-1;
  background-color: $color-base-white;
}
.hse-Tag_variant_primaryInvert.hse-Tag_focused,
.hse-Tag_variant_primaryInvert:focus {
  @include focus-shadow;
  color: $color-brand-1;
  background-color: $color-base-white;
}
.hse-Tag_variant_primaryInvert.hse-Tag_hovered,
.hse-Tag_variant_primaryInvert:hover {
  color: $color-brand-1;
  background-color: $color-element-overlay-light-op60;
}
.hse-Tag_variant_primaryInvert.hse-Tag_disabled,
.hse-Tag_variant_primaryInvert:disabled {
  color: $color-element-overlay-light-op60;
  border: 1px solid $color-element-overlay-light-op60;
  background-color: $color-element-overlay-light-op10;
}

.hse-Tag_variant_secondary {
  color: $color-gray-dusk-1;
  border: 1px solid $color-gray-dusk-5;
  background-color: transparent;
}

.hse-Tag_variant_secondary.hse-Tag_selected {
  color: $color-base-white;
  background-color: $color-brand-1;
}
.hse-Tag_variant_secondary.hse-Tag_focused,
.hse-Tag_variant_secondary:focus {
  @include focus-shadow;
  color: $color-brand-1;
  background-color: transparent;
}
.hse-Tag_variant_secondary.hse-Tag_hovered,
.hse-Tag_variant_secondary:hover {
  color: $color-brand-1;
  border: 1px solid $color-brand-1;
  background-color: transparent;
}
.hse-Tag_variant_secondary.hse-Tag_disabled,
.hse-Tag_variant_secondary:disabled {
  color: $color-gray-dusk-3;
  border: 1px solid $color-gray-dusk-4;
  background-color: $color-element-overlay-light-op10;
}

.hse-Tag_variant_secondaryInvert {
  color: $color-base-white;
  border: 1px solid $color-element-overlay-light-op10;
  background-color: transparent;
}

.hse-Tag_variant_secondaryInvert.hse-Tag_selected {
  color: $color-brand-1;
  background-color: $color-base-white;
}
.hse-Tag_variant_secondaryInvert.hse-Tag_focused,
.hse-Tag_variant_secondaryInvert:focus {
  @include focus-shadow;
  color: $color-base-white;
  background-color: transparent;
  &,
  &:hover {
    border-color: transparent;
  }
}
.hse-Tag_variant_secondaryInvert.hse-Tag_hovered,
.hse-Tag_variant_secondaryInvert:hover {
  color: $color-base-white;
  border: 1px solid $color-base-white;
  background-color: transparent;
}
.hse-Tag_variant_secondaryInvert.hse-Tag_disabled,
.hse-Tag_variant_secondaryInvert:disabled {
  color: $color-element-overlay-light-op60;
  border: 1px solid $color-element-overlay-light-op10;
  background-color: $color-element-overlay-light-op10;
}

.hse-Tag_hovered {
  color: $color-base-white;
  background-color: $color-brand-dark;
}

.hse-Tag_focused {
  outline: none;
}

.hse-Tag_clickable:not(.hse-Tag_disabled) {
  cursor: pointer;
  &:hover {
    color: $color-base-white;
    background-color: $color-brand-dark;
  }
  &:active {
    color: $color-base-white;
    background-color: $color-brand;
  }

  &:focus {
    outline: none;
  }
}

.hse-Tag_size_small {
  @include text_label_x_small_bold;
  height: $sizing-24;
  padding: $spacing-small-2x/2 $spacing-small-3x;
}
.hse-Tag_size_medium {
  @include text_caption_small_bold;
  height: $sizing-32;
  padding: $spacing-small-2x/2 $spacing-small-2x;
}
.hse-Tag_size_large {
  @include text_caption_medium_bold;
  height: 36px;
  padding: $spacing-small-3x 14px;
}

.hse-Tag__icon {
  text-align: center;
  vertical-align: -0.125em;
  text-transform: none;
  color: inherit;
  font-style: normal;
  line-height: 0;
}
.hse-Tag__icon_left {
  margin-right: $spacing-small-3x / 2;
}
.hse-Tag__icon_right {
  margin-left: $spacing-small-3x / 2;
}

.hse-Tag_selected {
  color: $color-base-white;
  background-color: $color-brand;
}
